		//ï¿½Ò²ï¿½ï¿½ï¿½ï¿½ï¿½ï¿½ï¿½
		asideScroll(e){
			if(!this.sizeCalcState){
				this.calcSize();
			}
			let scrollTop = e.detail.scrollTop;
			let tabs = this.slist.filter(item=>item.top <= scrollTop).reverse();
			if(tabs.length > 0){
				this.currentId = tabs[0].pid;
			}
		},
		//ï¿½ï¿½ï¿½ï¿½ï¿½Ò²ï¿½ï¿½ï¿½Ã¿ï¿½ï¿½tabï¿½Ä¸ß¶Èµï¿½ï¿½ï¿½Ï¢
		calcSize(){
			let h = 0;
			this.slist.forEach(item=>{
				console.log(item[0])
				let view = uni.createSelectorQuery().select("#main-" + item[0].id);
				console.log(view)
				view.fields({
					size: true
				}, data => {
					item[0].top = h;
					h += item[0].height;
					item[0].bottom = h;
				}).exec();
			})
			this.sizeCalcState = true;
		},
